html {
  width: 100%;
  background-color: #36454F;
  margin: 0;
  padding: 0;
}

h1,
h5 {
  padding: 2px;
  margin: 2px;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.wrap {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.box,
svg {
  padding: 10px;
  width: 250px;
  height: 250px;
}

img:hover,
svg:hover {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.footer {
  bottom: 0;
  position: fixed;
  font-size: 20px;
}